body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
    --background-color: white;
}
body {
    background-color: white;
    background-color: var(--background-color);
}
.App {
    text-align: center;
    width: 100%;
    height: 80vh;
}

.chart-view {
    min-height: 80%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.large-view {
    -webkit-flex: 1 1;
            flex: 1 1;
    position: relative;
    width: 100%;
    margin: 1rem 0;
}

.large-view svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.timeline-view {
    -webkit-flex: 0 0 100px;
            flex: 0 0 100px;
    max-height: 100px;
    position: relative;
    width: 100%;
    /*background-color: blue;*/
}

.timeline-view svg {
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

