:root {
    --background-color: white;
}
body {
    background-color: var(--background-color);
}
.App {
    text-align: center;
    width: 100%;
    height: 80vh;
}

.chart-view {
    min-height: 80%;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.large-view {
    flex: 1 1;
    position: relative;
    width: 100%;
    margin: 1rem 0;
}

.large-view svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.timeline-view {
    flex: 0 0 100px;
    max-height: 100px;
    position: relative;
    width: 100%;
    /*background-color: blue;*/
}

.timeline-view svg {
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

